
import {Options, Vue} from 'vue-class-component';

@Options({
    props:{
        toneId: Number,
        toneName: String,
        inputStatus: Boolean,
    },
    emits: {
        selectedId: Number
    }
})

export default class RingItem extends Vue {
    toneId!: number
    toneName!: string
    inputStatus!: boolean
    selectedId!: number
    // sound = '/src/assets/audio/alarm-clock.mp3' 

    ringChecked():void{
        this.$emit('selectedId', this.toneId)
        // new Audio(this.sound).play();
    }

}
