
import {Options, Vue} from 'vue-class-component';
import RingItem from '@/components/Ring/RingItem.vue';

@Options({
    components: {
        RingItem,
    },
    props:{
        title: String,
        selectedRing: Number,
    },
    emits: {
        newSelectedRing: Number
    }
})

export default class RingPanel extends Vue {
    title!: string
    selectedRing!: number
    newSelectedRing!: number
    selections = [
        'none', 'Default', 'alarm',
        'alert', 'beep', 'bell',
        'bird', 'bugle', 'digital',
        'drop', 'horn', 'music',
        'ring', 'warning', 'whistle'
    ]

    getSelectedId(value: number): void{
        this.$emit('newSelectedRing',value)
    }
}
